.list {
    display: flex;
    align-items: center;
    background-color: #1d1d1d;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
      0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
    text-transform: capitalize;
    z-index: 1300;
  }
  
  .link {
    display: inline-block;
    padding: 25px;
    color: #ffffff;
    font-size: 28px;
    font-weight: 500;
  }
  
  .activeLink {
    color: rgb(248, 100, 14);
  }
  